import { Component, OnInit, Input } from '@angular/core';
import { MatTab, MatTableDataSource } from '@angular/material';
import { Order } from '../model/Order';
import { OrderService } from '../service/order.service';
import { RouterEvent, Router } from '@angular/router';

@Component({
  selector: 'app-orders-table',
  templateUrl: './orders-table.component.html',
  styleUrls: ['./orders-table.component.scss']
})
export class OrdersTableComponent implements OnInit {

  private orderDataSource: MatTableDataSource<Order> = new MatTableDataSource<Order>(); 
  private $orders: Order[] = []; 
  private $selectedOrders: Order[] = []; 
  private selectAll: boolean = false; 

  public displayedColumns = [
      'recived', 
      'no',
      'drug',
      'quantity',
      'autorized',
      'ordered_quantity',
      'adjusted_quantity',
      'issued_quantity',   
      'expier_at',
      'is_recived',
      'recived_at']; 


  @Input() orders; 


  ngOnChanges(){
    this.$orders = this.orders;
    this.orderDataSource.data = this.orders;
  }
  constructor(private _order: OrderService, private _router: Router) { }

  ngOnInit() {
  }

  select(i, e){
    if(e.checked){
      this.$selectedOrders.push(this.$orders[i]);
      
      if(this.$selectedOrders.length == this.$orders.length){
        this.selectAll = true; 
      }
    }
    else{
      this.$selectedOrders.splice(this.$selectedOrders.indexOf(this.$orders[i]),1);
      this.selectAll = false; 
    }
    this.orderDataSource.data = this.orders;
  }

  _selectAll(e){
    this.selectAll = e.checked;
    if(e.checked){
      this.$orders.forEach(order => {
      });
      this.$selectedOrders = this.$orders; 
    }else{
      this.$selectedOrders.forEach(order => {
      });
      this.$selectedOrders = []; 
    }
    this.orderDataSource.data = this.orders;
  }

  recive(){
    this._order.recived(this.$selectedOrders).subscribe(
      responce => {
        this._router.navigate(["/drug"]); 
      }
    )
  }

}
